import React, {useEffect} from 'react';
import { connect } from 'react-redux'
import { getSets } from '../../reducers/preview'
import './select-set.scss'
import { Link } from "react-router-dom";



function SelectSet(props) {

  useEffect(() => {
    props.getSets()
  }, [])

  return (
    <div className="SelectSet">
      <h3>Select Art Set</h3>
      <ul>
        {props.sets.map(set => (
          <li>
            <Link to={set._id}>
              {set.name}
            </Link>
          </li>
        ))}
      </ul>
    </div>
  )
}


export default connect(
  state => ({
    sets: state.sets
  }),
  {
    getSets
  }
)(SelectSet)
