import React from 'react';
import FacePartContainer from '../components/face-part-container'
import { connect } from 'react-redux'
import { changeColor } from '../reducers/preview'
import ColorPicker from '../components/color-picker'

function Base({items, current, changeColor, selectPart, part, noColors}) {
  if (!(items && items[part] && items[part].images && items[part].images.length)) return 'No items on this layer'
  return (
    <div>
      { items[part].images.map(item => (
        <FacePartContainer
          active={current && current[part] && (current[part]._id === item._id)}
          key={item._id}
          id={item._id}
          part={part}
          url={item.value}
          selectPart={selectPart}
        />
      )) }
      <br/>
      { !items[part].inheritColorFrom && <ColorPicker part={part} /> }
    </div>
  )
}

export default connect(
  state => ({
    items: state.partsData,
    current: state.current
  }),
  dispatch => ({
    changeColor: (part, color) => dispatch(changeColor(part, color)),
    selectPart: (_id, part) => dispatch({ type: 'SELECT_PART', payload: {_id, part} })
  })
)(Base)
