import { createSelector } from 'reselect'
import { changeImageColor, createSprite } from '../services/canvas'
import * as backend from '../services/backend'
import { cloneDeep, pickBy } from 'lodash'
import { saveAs } from 'file-saver';

const TRANSPARENT_PIXEL = 'data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAQAAAC1HAwCAAAAC0lEQVR42mNkYAAAAAYAAjCB0C8AAAAASUVORK5CYII='
const TRANSPARENT_ID = 'transparent'

const initialState = {
  partsDataOriginal: {},
  partsData: {},
  PARTS: [],
  PARTS_WITH_COLORS: [],
  partSprites: [],
  current: {},
  previewIsLoading: false,
  facePartsAreLoading: true,
  savingIsLoading: false,
  sets: [],
  hiddenParts: [], // for conflicts
  conflicts: {},
  current: {}
  // current: {
  //   head: {
  //     _id: null,
  //     color: null
  //   }
  // }
};

export function createReducer(initialState, handlers) {
  return function reducer(state = initialState, action) {
    if (handlers.hasOwnProperty(action.type)) {
      return handlers[action.type](state, action)
    } else {
      return state
    }
  }
}

// ACTIONS

export const loadFaceParts = (setId) => (dispatch) => {
  dispatch({ type: 'LOAD_FACE_PARTS_REQUEST' });
  backend.getFaceParts(setId).then(response => {
    dispatch({ type: 'LOAD_FACE_PARTS_SUCCESS', payload: response });
    setTimeout(() => {
      dispatch(randomizeFace());
    }, 10);
  }).catch(e => {
    dispatch({ type: 'LOAD_FACE_PARTS_FAILURE', payload: e });
  })
}

export const getSets = () => (dispatch) => {
  dispatch({ type: 'GET_SETS_REQUEST' });
  backend.getSets().then(response => {
    dispatch({ type: 'GET_SETS_SUCCESS', payload: response });

  }).catch(e => {
    dispatch({ type: 'GET_SETS_FAILURE', payload: e });
  })
}

export const saveImage = (setId) => (dispatch, getState) => {
  const data = getState().current;

  // createSprite(getState().partsData[2].images.map(img => img.value)).then(res => {
  //   console.log(res);
  // })

  dispatch({ type: 'SAVE_IMAGE_REQUEST' });
  return new Promise((resolve, reject) =>  {
    backend.saveImage(setId, data).then(response => {
      saveAs(new Blob([response.data]), 'image' + Math.floor(Math.random() * 100000 + 1) + '.png');
      dispatch({ type: 'SAVE_IMAGE_SUCCESS', payload: response });
      resolve(response);
    }).catch(e => {
      dispatch({ type: 'SAVE_IMAGE_FAILURE', payload: e });
      reject(e);
    })
  });
}

export const changeColor = (part, color) => (dispatch, getState) => {
  let partsToInheritColor = [];
  getState().partsDataOriginal.forEach((pd, index) => {
    if (pd.inheritColorFrom === getState().partsDataOriginal[part]._id) {
      partsToInheritColor.push(index);
    }
  })
  getState().partsDataOriginal[part].images.forEach(el => {
    changeImageColor(el.value, color).then((value) => {
      dispatch({ type: 'SET_COLOR', payload: { _id: el._id, part, value, color } })
    })
  })
  partsToInheritColor.forEach(inheritPart => {
    getState().partsDataOriginal[inheritPart].images.forEach(el => {
      changeImageColor(el.value, color).then((value) => {
        dispatch({ type: 'SET_COLOR', payload: { _id: el._id, part: inheritPart, value, color } })
      })
    })
  })
}


export const selectPart = (part, _id) => (dispatch, getState) => {

}

export const randomizeFace = () => (dispatch, getState) => {

  //dispatch({ type: 'SET_PREVIEW_IS_LOADING', value: true})
  for (let index in getState().partsData) {
    if (!getState().PARTS_WITH_COLORS.includes(index)) continue;
    if (!getState().partsData[index].images) continue;
    for (let i = 0; i < getState().partsData[index].images.length; i++) {
      getState().partsData[index].images[i].isLoading = true;
    }
  }

  getState().PARTS_WITH_COLORS.forEach(part => {
    let r = Math.floor(Math.random() * 255).toString(16).padStart(2, '0')
    let g = Math.floor(Math.random() * 255).toString(16).padStart(2, '0')
    let b = Math.floor(Math.random() * 255).toString(16).padStart(2, '0')
    dispatch(changeColor(part, '#' + r + g + b))
  })

  let partsShuffled = [].concat(getState().PARTS).sort(() => Math.random() - 0.5);
  partsShuffled.forEach(part => {
    if (!getState().partsData[part].images.length) return

    let index = Math.floor(Math.random() * (getState().partsData[part].images.length - 1)) + 1;
    let _id = getState().partsData[part].images[index]._id;
    dispatch({type: 'SELECT_PART', payload: {_id, part }})
  })
}




// SELECTORS
//
export const getCurrentValues = createSelector(
  state => state.partsData,
  state => state.current,
  (partsData, current) => {
    let entries = Object.entries(partsData);
    let values = [];
    for (let i = 0; i < entries.length; i ++) {
      let key = entries[i][0];
      if (!current[key]) continue;
      let value = entries[i][1].images.find(el => el._id == current[key]._id)
      values.push({
        zIndex: entries[i][1].zIndex,
        value: value
      });
    }
    values = values.sort((a, b) => a.zIndex - b.zIndex)
    values = values.map(v => v.value && v.value.value)
    return values
  }
)

export const getPreviewIsLoading = createSelector(state => state.partsData, partsData => {
  let isLoading = false;
  if (!partsData) return true;
  for (let index in partsData) {
    for (let i = 0; i < partsData[index].images.length; i++) {
      if (partsData[index].images[i].isLoading) {
        isLoading = true;
      }
    }
  }
  return isLoading;
});

// REDUCER

const reducer = createReducer(initialState, {
  SELECT_PART: function(state, { payload: { _id, part }}) {
    let newCurrent = Object.assign({}, state.current);

    let partId = state.partsData[part]._id;

    if (state.conflicts[partId]) {
      state.conflicts[partId].forEach(conflictedPart => {
        for (let index in state.partsData) {
          if (state.partsData[index]._id === conflictedPart) {
            newCurrent[index] && (newCurrent[index]._id = TRANSPARENT_ID);
          }
        }
      });
    }
    return {
      ...state,
      current: {
        ...newCurrent,
        [part]: {
          ...state.current[part],
          _id: _id,
        }
      }
    }
  },
  SET_COLOR: function(state, { payload: {_id, part, value, color }}) {
    let newPartImages = [].concat(state.partsData[part].images)
    newPartImages.find(el => el._id === _id).value = value;
    newPartImages.find(el => el._id === _id).isLoading = false;
    return {
      ...state,
      partsData: {
        ...state.partsData,
        [part]: {
          ...state.partsData[part],
          images: newPartImages
        }
      },
      current: {
        ...state.current,
        [part]: {
          ...state.current[part],
          color
        }
      }
    }
  },
  SET_PREVIEW_IS_LOADING: function(state, { value }) {
    return {
      ...state,
      previewIsLoading: value
    }
  },
  GET_SETS_SUCCESS: function(state, { payload }) {
    return {
      ...state,
      sets: payload.data
    }
  },
  LOAD_FACE_PARTS_REQUEST: function(state, { payload }) {
    return {
      ...state,
      current: {},
      facePartsAreLoading: true
    }
  },
  LOAD_FACE_PARTS_SUCCESS: function(state, { payload }) {
    let newPartsData =  cloneDeep(payload.data);
    let conflicts = {};
    newPartsData.forEach(part => {
      part.images.unshift({
        _id: TRANSPARENT_ID,
        value: TRANSPARENT_PIXEL
      });
      part.images.forEach(img => {
        img.isLoading = false;
      })
      if (part.conflictsWith.length) {
        if (conflicts[part._id]) conflicts[part._id].concat(part.conflictsWith);
        else conflicts[part._id] = part.conflictsWith;
        conflicts[part._id] = part.conflictsWith;
        part.conflictsWith.forEach(conflictedPart => {
          if (conflicts[conflictedPart]) conflicts[conflictedPart].push(part._id);
          else conflicts[conflictedPart] = [part._id];
        })
      }
    })

    let sprites = cloneDeep(payload.data).map(layer => {
      return createSprite(layer.images.map(img => img.value));
    })

    return {
      ...state,
      conflicts,
      partsData: newPartsData,
      partsDataOriginal: cloneDeep(newPartsData),
      PARTS: Object.keys(payload.data),
      PARTS_WITH_COLORS: Object.keys(pickBy(payload.data, part => part.colors && part.colors.length)),
      sprites,
      facePartsAreLoading: false
    }
  },
  SAVE_IMAGE_REQUEST: function(state) {
    return {
      ...state,
      savingIsLoading: true
    }
  },
  SAVE_IMAGE_SUCCESS: function(state) {
    return {
      ...state,
      savingIsLoading: false
    }
  }
})

export default reducer
