const canvas = document.createElement("canvas"), // shared instance
  context = canvas.getContext("2d");

const IMG_WIDTH = 600;
const IMG_HEIGHT = 600;

// set image pixel size and hex color
canvas.width = IMG_WIDTH;
canvas.height = IMG_HEIGHT;

export const changeImageColor = (url, color) => {

  return new Promise((resolve, reject) => {
    let img = document.createElement('img');
    img.src = url;
    img.onload = () => {

      context.clearRect(0, 0, canvas.width, canvas.height);
      context.drawImage(img, 0, 0, canvas.width, canvas.height);
      colorize(color, 1);

      context.drawImage(img, 0, 0, canvas.width, canvas.height);

      resolve(canvas.toDataURL("image/png", 0.7));
    }
  })


  function colorize(color, alpha) {

    context.imageSmoothingQuality = "medium";
    context.globalCompositeOperation = "source-atop";
    context.fillStyle = color;
    context.fillRect(0, 0, canvas.width, canvas.height);
    context.globalCompositeOperation = "multiply";
  }
}

export const createSprite = (base64Urls) => {
  const spriteCanvas = document.createElement("canvas"),
    ctx = spriteCanvas.getContext("2d");
  spriteCanvas.width = base64Urls.length * IMG_WIDTH;
  spriteCanvas.height = IMG_HEIGHT;

  return new Promise((resolve) => {

    let promisesArray = [];

    for (let i = 0; i < base64Urls.length; i++) {
      promisesArray.push(drawImg(base64Urls[i], i * IMG_WIDTH, 0));
    }

    Promise.all(promisesArray).then(() => {
      resolve(spriteCanvas.toDataURL("image/png", 1));
    })
  });

  function drawImg(url, x, y) {
    return new Promise((resolve) => {
      let img = document.createElement('img');
      img.src = url;
      img.onload = () => {
        ctx.drawImage(img, x, y, IMG_WIDTH, IMG_HEIGHT);
        resolve();
      }
    })
  }
}




