import React from 'react';
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

import "react-tabs/style/react-tabs.css";
import './App.scss';
import AvatarPage from './components/avatar-page'
import SelectSet from './components/select-set'


export default class App extends React.Component {
  render() {
    return (
      <div className="App">
        {/*<Router basename='/avatar-platform'>*/}
        <Router>
          <Switch>
            <Route path="/" exact component={SelectSet} />
            <Route path="/:setName" exact component={AvatarPage} />
          </Switch>
        </Router>
      </div>
    );
  }
}


