import axios from 'axios';

const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:3001/';
console.log(API_URL);

const axiosInstance = axios.create({
  headers: {
    'Content-Type': 'application/json',
  }
});

export function getFaceParts(setId) {
  return axiosInstance.get(API_URL + 'face-parts/' + setId);
}

export function getSets() {
  return axiosInstance.get(API_URL + 'face-parts/sets');
}

export function saveImage(setId, data) {
  return axios({
    url: API_URL + 'save-image/' + setId,
    method: 'GET',
    responseType: 'blob', // important,
    params: { data }
  })
}
