import React from 'react';
import {connect} from 'react-redux';
import './main-tabs.scss'
import { Tab, TabList, TabPanel, Tabs } from 'react-tabs'
import Base from '../../face-parts/Base'

function MainTabs({ PARTS, partsData }) {
  if (!PARTS || !PARTS.length) {
    return <div></div>
  }
  return (
    <div className="MainTabs">
      <Tabs defaultIndex={0}>
        <TabList>
          {PARTS.map((part, index) => (
            <Tab key={index}>{partsData[part].title}</Tab>
          ))}
        </TabList>

        {PARTS.map((part, index) => (
          <TabPanel>
            <Base  key={index} part={part}/>
          </TabPanel>
        ))}

      </Tabs>
    </div>
  )
}

export default connect(
  state => ({
    PARTS: state.PARTS,
    partsData: state.partsData
  })
)(MainTabs)
