import React, {useEffect} from 'react';
import { connect } from 'react-redux'
import { randomizeFace, saveImage } from '../../reducers/preview'
import './avatar-page.scss'

import Preview from '../../components/preview'
import ButtonActions from '../../components/button-actions'
import { loadFaceParts } from '../../reducers/preview'
import MainTabs from '../../components/main-tabs'


function AvatarPage(props) {

  useEffect(() => {
    props.loadFaceParts(props.match.params.setName);
  }, []);

  return (
    <div className="AvatarPage">
      <aside className="App-left-col">
        <div className="left-col-container">
          <Preview setName={props.match.params.setName}/>
          <br/>
          <ButtonActions setName={props.match.params.setName}/>
        </div>
      </aside>
      <main className="App-main">
        <MainTabs />
      </main>
    </div>
  )
}


export default connect(
  null,
  {
    loadFaceParts
  }
)(AvatarPage)
