import React, {useEffect} from 'react';
import './download-success-modal.scss'
import Modal from 'react-modal';

Modal.setAppElement('#root')

export default function DownloadSuccessModal(props) {

  return (
    <Modal
      isOpen={props.isOpen}
      onRequestClose={props.closeModal}
      contentLabel="Example Modal"
      className="download-success-modal"
    >
      <h4>Great! Your download has been successful!</h4>
      <img className="modal-image" src={props.imgBase64} />
      <h6>If you want to download images in high quality, you can <strong>become premium</strong></h6>
    </Modal>
  )
}
